    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    margin: 0 auto;
    background-color: rgba(14, 44, 100, 0.5);
    padding: 20px;
    gap: 20px;
    border-radius: 20px;
    max-width: 750px;
    text-align: center;
    }

.fade-in {
    opacity: 0;
    transition: opacity 0.5s ease;
    }
      
.fade-in.active {
    opacity: 1;
    }    

.welcome {
    display: flex;
    justify-content: center;
    font-size: x-large;
    font-family:Arial, Helvetica, sans-serif;
    justify-content: center;
    color: white;
}

#welcome {
    display: flex;
    font-size: large;
    letter-spacing: 0.1em;
    line-height: 30px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 20px;
   }

#title {
    letter-spacing: 1em;
    font-size: xx-large;
    font-weight: bold;
    text-align: center;
}

